import React, { useState, useEffect } from "react";
import {
  Chip,
  Divider,
  Grid,
  IconButton,
  InputBase,
  LinearProgress,
  Paper,
  Typography,
} from "@material-ui/core";
import { Search as SearchIcon } from "@material-ui/icons";

import "./Skills.scss";
import SkillsPlaceholder from "./SkillsPlaceholder";

const Excerpt = ({ data }) => {
  const fdata = Object.keys(data)
    .map((k) => {
      const dtypes = data[k];
      return dtypes.filter((d) => d.show);
    })
    .flat();
  return fdata.map((d) => (
    <Chip size="medium" className="excerpt-item" key={d.name} label={d.name} />
  ));
};

const onSearch = ({ target: { value } }, data, setSdata) => {
  if (!value) {
    setSdata(data);
  }
  setSdata(
    Object.keys(data)
      .map((k) => {
        const dtypes = JSON.parse(JSON.stringify(data[k]));
        return {
          [k]: dtypes.filter((d) => new RegExp(value, "gi").test(d.name)),
        };
      })
      .reduce((a, b) => ({ ...a, ...b }))
  );
};

const Search = ({ data, setSdata }) => {
  return (
    <Paper
      component="div"
      style={{
        background: "transparent",
        boxSizing: "border-box",
        position: "relative",
        padding: "2.5px 40px",
        height: 55,
        display: "flex",
        alignItems: "center",
        justifyContent: "flex-start",
        width: "90%",
      }}
    >
      <InputBase
        placeholder="Search Skills"
        inputProps={{ "aria-label": "search skills" }}
        style={{ width: "77%" }}
        onChange={(e) => onSearch(e, data, setSdata)}
      />
      <IconButton
        disabled
        aria-label="search"
        style={{ position: "absolute", right: 0 }}
      >
        <SearchIcon />
      </IconButton>
    </Paper>
  );
};

const type = (t, ss) => (
  <div key={t}>
    {ss[0] && (
      <>
        <h2 className="all-title">
          <i className="fa fa-spinner all-icon" />
          <div>{t}</div>
        </h2>
      </>
    )}
    {ss.map((s) => (
      <Grid key={s.name} container spacing={0} className="skillbar">
        <Grid item sm={5} md={5} lg={4} className="skillbar-title">
          <Typography variant="subtitle2">{s.name}</Typography>
        </Grid>
        <Grid item sm={7} md={7} lg={5} className="skillbar-bars">
          {[...Array(5).keys()].map((i) => {
            const number = (5 - i) * 20 - (100 - s.amount);
            return (
              <div key={i} className="mini-bar">
                <LinearProgress
                  variant="determinate"
                  value={Math.min(100, Math.max(0, (number / 20) * 100))}
                  style={{ height: 8, borderRadius: 2 }}
                />
              </div>
            );
          })}
        </Grid>
      </Grid>
    ))}
    {ss[0] && <Divider />}
  </div>
);

export default function Skills(props) {
  const { loader } = props;
  const [data, setData] = useState(null);
  const [sdata, setSdata] = useState({});
  useEffect(() => {
    setTimeout(() => {
      window.scrollTo(0, 0);
    }, 100);
  }, []);
  useEffect(() => {
    const fetchData = async () => {
      const res = await fetch("./assets/data/skills.json");
      const data = await res.json();
      setData(data);
      setSdata(data);
    };
    loader(true);
    fetchData();
    loader(false);
  }, [loader]);

  if (!data) {
    return <SkillsPlaceholder />;
  }

  return (
    <div id="Skills">
      <div className="excerpt">
        <Typography variant="subtitle1">More Experienced</Typography>
        <Divider />
        <br />
        <Grid container>
          <Grid item sm={12} md={10} lg={8}>
            <Excerpt data={data} />
          </Grid>
        </Grid>
        <br />
      </div>
      <Divider />
      <div className="search">
        <Search data={data} setSdata={setSdata} />
      </div>
      <Divider />
      <div className="all">
        {Object.keys(sdata).map((e) => type(e, sdata[e]))}
      </div>
    </div>
  );
}
