import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import { Skeleton } from "@material-ui/lab";
import Drawer from "@material-ui/core/Drawer";
import Divider from "@material-ui/core/Divider";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Typography from "@material-ui/core/Typography";

// import Logo from '../assets/images/logo.png';
import "./Header.scss";

const pages = [
  {
    title: "Home",
    icon: <i className="fab fa-houzz" />,
    url: "/home",
  },
  {
    title: "Background",
    icon: <i className="far fa-window-maximize" />,
    url: "/background",
  },
  {
    title: "Skills",
    icon: <i className="fas fa-code" />,
    url: "/skills",
  },
  {
    title: "Projects",
    icon: <i className="fas fa-project-diagram" />,
    url: "/projects",
  },
  {
    title: "Blog",
    icon: <i className="fas fa-blog" />,
    url: "/blog",
  },
];

const drawer = (
  { name, avatar, lastupdate },
  imgPath,
  { active, setActive }
) => {
  return (
    <div className="drawer-content">
      <div className="avatar">
        {!avatar && <Skeleton variant="circle" width={175} height={175} />}
        {avatar && <img src={`${imgPath}${avatar}`} alt={name} />}
      </div>
      <Divider />
      <Divider />
      <Divider />
      <Divider />
      <div className="name">
        {!name && (
          <Skeleton
            variant="text"
            width="70%"
            height={70}
            style={{ margin: "auto" }}
          />
        )}
        {name && (
          <Typography align="center" variant="h4" component="h1">
            {name}
          </Typography>
        )}
      </div>
      <Divider />
      <Divider />
      <Divider />
      <Divider />
      <List>
        {pages.map((page, index) => {
          if (page.title !== "Blog")
            return (
              <NavLink
                onClick={() => setActive(index)}
                key={page.title}
                style={{ textDecoration: "none", color: "inherit" }}
                to={page.url}
              >
                <ListItem
                  button
                  className={index === active ? "activated" : ""}
                >
                  <ListItemIcon>{page.icon}</ListItemIcon>
                  <ListItemText primary={page.title} />
                </ListItem>
              </NavLink>
            );
          return (
            <a
              style={{ textDecoration: "none", color: "inherit" }}
              key={page.title}
              href={page.url}
              target="_blank"
              rel="noopener noreferrer"
            >
              <ListItem button className={index === active ? "activated" : ""}>
                <ListItemIcon>{page.icon}</ListItemIcon>
                <ListItemText primary={page.title} />
              </ListItem>
            </a>
          );
        })}
      </List>
      <Divider />
      <Divider />
      <Divider />
      <Divider />
      <div className="last-update">
        <code>Last Update: {lastupdate}</code>
      </div>
      {/* <a href="http://en.ce.kntu.ac.ir" target="_blank" rel="noopener noreferrer"><div className="logo"><img src={Logo} alt={`${name}-logo`} /></div></a> */}
      <Divider />
      <Divider />
      <Divider />
      <Divider />
      {/* <div className="developer">
        <Typography align="center" variant="caption">Developed By: <u><a href="https://alireza-kavian.github.io">Alireza Kavian</a></u></Typography>
      </div> */}
    </div>
  );
};

export default function Header(props) {
  let activeInitVal = 0;
  pages.forEach(
    (page, idx) =>
      (activeInitVal = window.location.href.includes(page.url.replace("/", ""))
        ? idx
        : activeInitVal)
  );

  const [active, setActive] = useState(activeInitVal);
  const { name, avatar, lastupdate, imgPath } = props;
  const small = window.innerWidth <= 960 ? true : false;
  const anchor = small ? "top" : "left";
  const variant = small ? "persistent" : "permanent";
  return (
    <Drawer
      className="drawer"
      variant={variant}
      anchor={anchor}
      open
      ModalProps={{
        keepMounted: true, // Better open performance on mobile.
      }}
    >
      {drawer({ name, avatar, lastupdate }, imgPath, { active, setActive })}
    </Drawer>
  );
}
